import { useMemo } from "react";
import Tags from "./Tags";
import decode from "./lib/decode-html";
import styles from "./Post.module.css";
import { DateTime } from "luxon";
import { Link } from "react-router-dom";
import ReactGA from "react-ga";
import PostContent from "./PostContent";

const onClick = (e) => {
	const targetId = e.target.getAttribute("data-id");

	if (targetId) {
		ReactGA.event({
			category: "Visit",
			action: e.target.getAttribute("data-id"),
		});
	}

	return false;
};

function PostLink({ children, to, id, external }) {
	return (
		<>
			{external && (
				<a href={to} target="_blank" rel="noreferrer" onClick={onClick} data-id={id}>
					{children}
				</a>
			)}
			{!external && (
				<Link className={styles.PermaLink} to={to}>
					{children}
				</Link>
			)}
		</>
	);
}

function Post(props) {
	const { post, external } = props;

	const dateTime = DateTime.fromISO(post.date);
	const date = dateTime.toLocaleString(DateTime.DATETIME_SHORT);

	const diff = dateTime.diffNow("days").toObject();
	const dateOffset = diff.days > -10 ? dateTime.toRelative() : null;

	const targetLink = useMemo(() => (external ? post.link : `/view/${post.id}/`), [post, external]);

	return (
		<>
			<div key={post.id} className={styles.Post}>
				<div className={styles.imageWrapper}>
					<PostLink to={targetLink} id={post.id} external={external}>
						<img src={"/" + post.image} alt={post.title} className={styles.Image} />
					</PostLink>
					{dateOffset && <div className={styles.DateMark}>{dateOffset}</div>}
				</div>
				<h2 className={styles.PostHeader}>
					<PostLink to={targetLink} id={post.id} external={external}>
						{decode(post.title)}
					</PostLink>
				</h2>
				<PostContent text={decode(post.content)} />
				<Tags tags={post.tags} />
				<div className={styles.Footer}>
					<ul>
						<li>
							<span className={styles.DateInfo}>{date}</span>
						</li>
						<li>
							<a href={post.link} target="_blank" rel="noreferrer" className={styles.readMore} onClick={onClick} data-id={post.id}>
								 
							</a>
						</li>
					</ul>
				</div>
			</div>
			{external && (
				<div className={styles.PostInfo}>
					<a href={post.link} target="_blank" rel="noreferrer" className={styles.readMore} onClick={onClick} data-id={post.id}>
						  {post.link}   .
						<br />
						    .
					</a>
				</div>
			)}
		</>
	);
}

export default Post;
