import styles from "./Tags.module.css";

function Tags(props) {
	const { tags } = props;
	return (
		<div className={styles.Tags}>
			{tags &&
				Array.isArray(tags) &&
				tags.map((tag) => (
					<span key={tag} className={styles.Tag}>
						#{tag}
					</span>
				))}
		</div>
	);
}

export default Tags;
