import { Link } from "react-router-dom";
import styles from "./Nav.module.css";

function Nav(props) {
	const { prevLink, nextLink } = props;

	return (
		<div className={styles.Nav}>
			<ul>
				<li>
					{prevLink && (
						<Link className={styles.NavButton} to={prevLink}>
							Prev
						</Link>
					)}
					{!prevLink && (
						<span className={styles.NavButtonDisabled}>Prev</span>
					)}
				</li>
				<li>
					{nextLink && (
						<Link className={styles.NavButton} to={nextLink}>
							Next
						</Link>
					)}
					{!nextLink && (
						<span className={styles.NavButtonDisabled}>Next</span>
					)}
				</li>
			</ul>
		</div>
	);
}

export default Nav;
