import { BlogContext } from "./BlogContext";
import { useContext, useMemo, useEffect } from "react";
import Post from "./Post";
import styles from "./Posts.module.css";
import BackNav from "./BackNav";
import SEOSupport from "./SEOSupport";
import decode from "./lib/decode-html";
import { useParams } from "react-router-dom";

export default function PostView() {
	const { id = null } = useParams();
	const posts = useContext(BlogContext);
	const post = useMemo(() => posts.find((p) => p.id === id), [posts, id]);

	useEffect(() => window.scrollTo(0, 0), []);

	return (
		<>
			{post && <SEOSupport keywords={post.tags} description={decode(post.title)} title={post.title} image={"/" + post.image} />}
			<BackNav />
			<div className={styles.Posts}>{post && <Post post={post} external />}</div>
			<BackNav />
		</>
	);
}
